.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.hide-scrollbar::-webkit-scrollbar {
  width: 0;
}

.hide-scrollbar::-webkit-scrollbar-thumb {
  background-color: transparent;
}
.hide-scrollbar::-webkit-scrollbar-track {
  background-color: transparent;
}
.app-brand.demo {
  position: sticky;
  top: 0;
  z-index: 99;
  background: white;
}
.fpopic {
  object-fit: contain;
  background-color: rgba(128, 128, 128, 0.279);
  /* max-width: 200px;
  max-height: 200px;
  min-height: 50px;
  min-width: 50px; */
  border-radius: 50%;
  overflow: hidden;
  height: 15vw;
  width: 15vw;
}
.fpo-name-heading {
  color: #fe5e00;
}
.fpo-icons {
  font-size: 20px;
  color: #696cff;
}
.fpo-icons1 {
  font-size: 20px;
  color: #63d10f;
}
.fpo-icons2 {
  font-size: 20px;

  color: #fe5e00;
}
.fpo-info {
  font-size: 25px;
  color: black;
}
i.fpo-icons,
i.fpo-icons1,
i.fpo-icons2 {
  /* width: 100%; */
  font-size: 30px;
}
img.intoggle-table-image {
  width: 50px;
  height: 80px;
  object-fit: contain;
}
.add-cat-img-preview img {
  width: 100%;
  height: 220px;
  object-fit: contain;
}
.add-cat-img-left img {
  width: 100%;
  object-fit: contain;
}
